@font-face {
  font-family: "Droid Serif";
  src: local("Droid Serif"), url("../fonts/DroidSerif.woff2") format("woff2"), url("../fonts/DroidSerif.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Droid Serif";
  src: local("Droid Serif"), url("../fonts/DroidSerif-Bold.woff2") format("woff2"), url("../fonts/DroidSerif-Bold.woff") format("woff");
  font-weight: 700;
  font-display: swap;
}
@font-face {
  font-family: "Comfortaa";
  src: local("Comfortaa-Regular"), url("../fonts/Comfortaa-Regular.woff2") format("woff2"), url("../fonts/Comfortaa-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "HelveticaNeueCyr";
  src: local("HelveticaNeueCyr"), url("../fonts/helveticaneuecyr-light.woff2") format("woff2"), url("../fonts/helveticaneuecyr-light.woff") format("woff");
  font-weight: 300;
  font-display: swap;
}
@font-face {
  font-family: "HelveticaNeueCyr";
  src: local("HelveticaNeueCyr"), url("../fonts/helveticaneuecyr-medium.woff2") format("woff2"), url("../fonts/helveticaneuecyr-medium.woff") format("woff");
  font-weight: 500;
  font-display: swap;
}
:root {
  --font-family: "HelveticaNeueCyr", sans-serif;
  --second-family: "Comfortaa", sans-serif;
  --third-family: "Arial", sans-serif;
  --1: #ff8a00;
  --2: #282626;
  --3: #fff;
  --4: #f2f0f1;
  --shadow-button: 0 4px 6px 0 rgba(0, 0, 0, 0.25);
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: sans-serif;
  font-family: "Droid Serif";
  min-width: 320px;
  font-size: 20px;
  font-weight: 400;
  position: relative;
}

address {
  font-style: normal;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

button {
  cursor: pointer;
  padding: 0;
}

input,
textarea,
select {
  font-family: inherit;
}

a {
  color: inherit;
  text-decoration: none;
  display: block;
}

ul,
ol {
  list-style: none;
  padding: 0;
  margin: 0;
}

figure {
  margin: 0;
  padding: 0;
}

fieldset {
  margin: 0;
  padding: 0;
  border: none;
}

h1,
h2,
h3,
p {
  margin: 0;
}

iframe {
  border: none;
}

section {
  padding-top: 90px;
}

/*
Доступно скрываю элементы
*/
.visually-hidden {
  position: fixed;
  transform: scale(0);
}

/*
Пример контейнера

Если контейнер 1180px по макету,
то для max-width прибавляем паддинги 30px*2, 
в итоге 1240px
Чтобы контент не прижимался к краям экрана
*/
.container {
  max-width: 1440px;
  padding: 0 130px;
  margin: 0 auto;
  position: relative;
}

.btn-shadow {
  filter: drop-shadow(4px 6px 3px rgba(0, 0, 0, 0.25));
  transition: filter 0.5s ease;
}

.btn-shadow:hover,
.btn-shadow:active {
  filter: drop-shadow(4px 6px 3px rgba(255, 138, 0, 0.25));
}

.global-size-img {
  width: 100%;
  height: 100%;
}

@media (max-width: 1024px) {
  .container {
    max-width: 1024px;
    padding: 0 72px;
    margin: 0 auto;
  }
  .visually-hidden-1000px {
    position: fixed;
    transform: scale(0);
  }
  section {
    font-display: swap;
    padding-top: 70px;
  }
  .global-size-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}
@media (max-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 34px;
    margin: 0 auto;
  }
  section {
    padding-top: 60px;
  }
}
@media (max-width: 480px) {
  .container {
    max-width: 320px;
    padding: 0 25px;
    margin: 0 auto;
  }
  .visually-hidden-320px {
    position: fixed;
    transform: scale(0);
  }
  section {
    padding-top: 40px;
  }
}
.header__navigate {
  background-color: var(--4);
  max-width: 394px;
  height: 394px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  z-index: 50;
  top: 110px;
  position: absolute;
}

.navigate__visually-hidden {
  transform: scale(0);
  transition: transform 0.3s ease, opacity 0.5s ease;
  opacity: 0;
}

.navigate__visible {
  transform: scale(1);
  opacity: 1;
  transition: transform 0.3s ease, opacity 0.5s ease;
}

.header__navigate::before {
  content: "";
  display: block;
  background-image: url("../img/triangle.svg");
  width: 52px;
  height: 30px;
  position: absolute;
  z-index: 0;
  left: 0;
  top: -25px;
}

.header__navigate::after {
  content: "";
  display: block;
  width: 394px;
  height: 10px;
  background-color: var(--1);
}

.header__navigate-list {
  padding: 70px 0 0 39px;
  display: flex;
  flex-direction: column;
  gap: 31px;
  position: relative;
  z-index: 50;
}

.header__navigate-item {
  position: relative;
}

.header__item-link {
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 20px;
  display: block;
  line-height: 150%;
  color: var(--2);
}

.line-item::after {
  content: "";
  display: block;
  width: 334px;
  right: 40px;
  height: 1px;
  top: 45px;
  position: absolute;
  background-color: var(--1);
}

.navigate__button-menu-svg-close {
  display: none;
  width: 33px;
  height: 25px;
  fill: var(--2);
  transition-duration: fill 0.3s ease;
}

.navigate__button-menu-svg-close:active {
  fill: #5b5656;
}

.header__navigate-item:last-child {
  display: none;
}

@media (max-width: 1024px) {
  .header__navigate {
    max-width: 349px;
    height: 320px;
  }
  .header__navigate::after {
    width: 349px;
  }
  .header__navigate-list {
    padding: 40px 0 0 39px;
  }
  .header__item-link {
    font-size: 18px;
    line-height: 133%;
  }
  .line-item::after {
    width: 309px;
    right: 20px;
  }
}
@media (max-width: 768px) {
  .header__navigate {
    height: 388px;
  }
  .header__navigate-item:last-child {
    display: block;
    gap: 13px;
  }
  .header__item-link {
    display: flex;
    gap: 13px;
    align-items: center;
  }
}
@media (max-width: 480px) {
  .header__navigate {
    max-width: 320px;
    height: 326px;
    top: 64px;
    left: 0;
  }
  .header__navigate::before {
    background-image: none;
  }
  .header__navigate::after {
    width: 320px;
    height: 10px;
  }
  .header__navigate-list {
    padding: 20px 0 0 35px;
  }
  .header__item-link {
    font-size: 14px;
    line-height: 143%;
  }
  .line-item::after {
    width: 270px;
  }
  .navigate__button-menu-svg-close {
    display: block;
  }
  .toggle-svg {
    display: none;
  }
  .header__item-link {
    gap: 10px;
  }
}
