.banner__grid {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: repeat(2, min-content);
  gap: 0 20px;
}

.banner__h1 {
  grid-column: 2/6;
  grid-row: 1/2;
  padding-top: 137px;
  font-family: var(--second-family);
  font-weight: 400;
  font-size: 60px;
  line-height: 100%;
  color: var(--1);
}

.banner__span {
  font-weight: 300;
  font-size: 40px;
}

.banner__span:nth-child(2) {
  font-weight: 300;
  font-size: 40px;
  color: var(--2);
}

